<template>
    <div>
        <div
                class="sign-up-header">
            <div class="mark-logo profile-nav-bg"
                 style="width: 40%;position: absolute;right: 0px;
              border: none;
    box-shadow: none;

          top: 1px;">
                <img src="images/bg-profile.jpg" alt="header" style="    position: absolute;
    right: 0px;">
            </div>
        </div>
        <a-card
                :bordered="false"
                class="card-signup header-solid"
                :bodyStyle="{ paddingTop: 0 }">
            <template #title>
            </template>
            <div class="divider my-25">
                <hr class="gradient-line"/>
                <p class="font-semibold text-muted"><span class="label">login form</span></p>
            </div>

            <a-form
                    id="components-form-demo-normal-login"
                    :form="form"
                    class="login-form"
                    @submit="handleLogin">
                <template v-if="error_msg">
                    <a-alert class="mb-10" :message="error_msg" type="error" closable/>
                </template>
                <a-form-item class="mb-10">
                    <a-input
                            v-decorator="[
              'user_email',
              {
                rules: [{ type: 'email', required: true }],
              },
            ]"
                            placeholder="البريد الإلكتروني"
                    />
                </a-form-item>
                <a-form-item class="mb-5">
                    <a-input
                            v-decorator="[
              'user_pass',
              {
                rules: [
                  { required: true, message: 'الرجاء ادخال كلمة المرور' },
                ],
              },
            ]"
                            type="password"
                            placeholder="Password"/>
                </a-form-item>
                <a-form-item class="mb-10">
                    <a-checkbox
                            v-decorator="[
              'remember',
              {
                valuePropName: 'checked',
                initialValue: true,
              },
            ]">
                        Remember me
                    </a-checkbox>
                </a-form-item>
                <a-form-item>
                    <a-button
                            type="primary"
                            block
                            html-type="submit"
                            class="login-form-button"
                            :loading="loading">
                        Login
                    </a-button>
                </a-form-item>
            </a-form>
            <!-- / Sign In Form -->
        </a-card>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                form: this.$form.createForm(this, {name: "login"}),
                errors: {},
                error_msg: "",
                loading: false,
            };
        },
        methods: {
            handleLogin(e) {
                e.preventDefault();
                this.error_msg = "";
                this.loading = true;
                this.form.validateFields((err, user) => {
                    if (!err) {
                        if (user.user_email && user.user_pass) {
                            console.log(user)
                            this.$store.dispatch("auth/login", user).then((response) => {
                                    console.log(response)
                                    this.loading = false;

                                    this.$router.push("/dashboard");
                                })
                                .catch((error) => {
                                    this.loading = false;
                                    console.log(error)

                                    this.error_msg =
                                        (error.response && error.response.data.message) ||
                                        error.message;
                                    error.toString();
                                });
                        }
                    } else {
                        this.loading = false;
                        this.error_msg = "Please Check errors and try again.";
                        return;
                    }
                });
                this.loading = false;

            },
        },
    };
</script>

<style lang="scss">
</style>